'use client'; // Indicates this is a Client Component in Next.js

import Link from 'next/link'; // Next.js component for client-side navigation
import { useState } from 'react'; // React hook for managing state
import ImageAvatars from '../avatar/avatorimge'; // Custom component for displaying an avatar image

const Header = () => {
  // State to manage the visibility of the mobile menu
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  // Function to toggle the mobile menu open/close
  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  return (
    // Header container with dark background, padding, and flex layout
    <header className="bg-transparent text-white p-4 flex justify-between items-center">
      {/* Logo or brand link with the avatar image and text */}
      <Link href="/" className="text-sm font-bold">
        <ImageAvatars /> Antnhe
      </Link>

      {/* Button to toggle the mobile menu on smaller screens */}
      <button className="text-white" onClick={toggleMobileMenu}>
        {/* Hamburger menu icon using an SVG */}
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>

      {/* Mobile menu dropdown, conditionally rendered based on `isMobileMenuOpen` state */}
      {isMobileMenuOpen && (
        <div className="absolute top-16 right-4 bg-transparent  p-4 rounded-md shadow-md z-10">
          {/* List of navigation links with padding and explicit text color */}
          <Link href="/support" className="block py-2 text-white">
            Support
          </Link>
          <Link href="/projects" className="block py-2 text-white">
            More Projects
          </Link>
          <Link href="/faqs" className="block py-2 text-white">
            FAQS
          </Link>
          <Link href="/privacy" className="block py-2 text-white">
            Privacy Terms
          </Link>
          <Link href="/client-support" className="block py-2 text-white">
            Client Support
          </Link>
          <Link href="/site-map" className="block py-2 text-white">
            Site Map
          </Link>
          <Link href="/tutorials" className="block py-2 text-white">
            Tutorials
          </Link>
          <Link href="/subscriptions" className="block py-2 text-white">
            Subscriptions
          </Link>
          <Link href="/contact" className="block py-2 text-white">
            Contact
          </Link>
        </div>
      )}
    </header>
  );
};

export default Header; // Export the Header component for use in other files